<template>
  <div>
    <BMainTopComponent :isShowBackBtn="true" />
    <el-container>
      <el-main>
        <div class="cloud-message-push"></div>
      </el-main>
      <toggle-crew ></toggle-crew>
    </el-container>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

import ToggleCrew from '@/components/toggleCrew/list'

export default {
  name: 'CloudMessagePush',
  components: {
    ToggleCrew
  },
  data() {
    return {}
  },
  props: [''],
  inject: [''],
  provide() {
    return {}
  },
  computed: {
    ...mapGetters(['selectCrew'])
  },
  watch: {
    selectCrew: {
      handler: function (nv) {
        console.log('crew changed', nv)
      },
      deep: true,
      immediate: true
    }
  },
  methods: {},
  beforeCreate() {},
  created() {},
  beforeMount() {},
  mounted() {},
  beforeUpdated() {},
  updated() {},
  beforeDestroy() {},
  destroyed() {}
}
</script>
<style lang='' scoped>
</style>


